let taskInput = document.querySelector("#taskInput");
let taskAdd = document.querySelector("#taskAdd");
let taskRemoveAll = document.querySelector("#taskRemoveAll");

let taskList = document.querySelector(".taskList");
let deadLine = document.querySelector("#deadLine");

taskAdd.addEventListener("click",addTask,false);
taskRemoveAll.addEventListener("click", removeAllTask, false);



let data = [];

function addTask(e){
  e.preventDefault();
  data.push({
    text:taskInput.value,
    deadline:deadLine.value
  });
  taskInput.value = '';
  deadLine.value = '';
  render();
}

function removeTask(e) {
  e.preventDefault();
  data.splice(e.target.dataset.i,1);
  console.log(e.target.dataset.i, 1);
  render();
}

function render() {
  let string = '';
  data.forEach(function (item, i) {
    string += `
      <tr>
        <th scope="row" class="text-center">${i+1}</th>
        <td>${item.text}</td>
        <td class="text-center">${item.deadline}</td>
        <td class="text-center"><button class="taskRemove text-center border-0">刪除任務</button></td>
      </tr>`;
  });
  taskList.innerHTML = string;

  let taskRemove = document.querySelectorAll(".taskRemove");
  taskRemove.forEach(function (item) {
    item.addEventListener("click", removeTask, false);
  })


}

function removeAllTask(e){
  e.preventDefault();
  data = [];
  render();
}
